:host(limel-example-markdown-composite) {
    display: flex;
    flex-direction: column;
    gap: 1rem;

    limel-input-field {
        height: 10rem;
    }

    fieldset {
        padding: 0.5rem 0.5rem 3rem 0.5rem;
        border-color: rgba(var(--contrast-1000), 0.3);
        border-radius: 0.25rem;

        legend {
            color: rgb(var(--contrast-1000));
        }
    }
}
